import React, { useState, useContext, useEffect } from 'react'
import { xcContext } from './xcContext'
import ItemInList, { DATETIME_OPTIONS } from './ItemInList'
import LoadingSpinner from './LoadingSpinner'

const ListViewBase = function ({children}) {
    if (children.length) {
        return (
        <article className='d-flex pt-0 px-3 flex-column'>
            {children}
        </article>
    )}
    else {
        return <LoadingSpinner/>
    }
}

const useDB_task_list = function(filters, sort_fn) {
    
    const [task_list, set_task_list] = useState(null)
    const xc = useContext(xcContext)
    if (!sort_fn) {
        sort_fn = (arg) => arg
    }
    const handleReply = function(results) {
        let new_task_list = list_from_map(results)
        sort_fn(new_task_list)
        set_task_list(new_task_list)
        console.log('%cupdating list', "color: white; background-color: blue; padding: 5px;")
    }
    useEffect(
        () => {
            xc.get_tasks({filters: filters, callback: handleReply})
        }, // eslint-disable-next-line
        []
    )

    return task_list
}

const formatListToJSX = function({task_list, format=null, item_options}) {
    if (!format) {
        format = (list) => {
            let new_list = []
            list.forEach(
                (task) => {
                    new_list.push( <ItemInList t={task} key={task.id} options={item_options}/> )
                }
            )
            return new_list
        }
    }

    let formatted_list = []
    if (task_list) {
        let new_list = Array.from(task_list)
        formatted_list = format(new_list)
    }
    return (
        <ListViewBase>
            {formatted_list}
        </ListViewBase>
    )
}

export function ListViewAll() {

    console.log('render ListViewAll')

    const task_list = useDB_task_list()
    return formatListToJSX({task_list})
    
}

export function ListViewImportant() {

    console.log('render ListViewImportant')
    
    const FILTER_BY = useContext(xcContext).FILTER_BY
    const task_list = useDB_task_list(
        [FILTER_BY.started(), FILTER_BY.not_done()],
        SORT_BY.PRIORITY
    )
    const item_options={dateTime: DATETIME_OPTIONS.DATE_ONLY}
    return formatListToJSX({task_list, item_options})
}

export function ListViewUrgent() {

    console.log('render ListViewUrgent')
    
    const FILTER_BY = useContext(xcContext).FILTER_BY
    const task_list = useDB_task_list(
        [FILTER_BY.started(), FILTER_BY.not_done()],
        SORT_BY.DUE
    )
    const format = (list) => {
        let new_list = []
        let last_full_day = null
        list.forEach(
            (task) => {
                let task_due_day = task.dueDate.toDateString()
                if (task_due_day !== last_full_day) {
                    new_list.push(
                    <>
                        <hr className='mb-1'/>
                        <span className='mx-auto fs-6 text-body-secondary'>{task_due_day}</span>
                    </>
                )
                    last_full_day = task_due_day
                }
                new_list.push(
                    <ItemInList t={task} key={task.id} options={{dateTime: DATETIME_OPTIONS.TIME_ONLY}}/>
                )
            }
        )
        return new_list
    }

    return formatListToJSX({task_list, format})
}

export const list_from_map = (task_map) => {
    // Make list of tasks from map of tasks
    let list = []
    task_map.forEach((value, key)=>{
        list.push(value)
    })
    return list
}

export const SORT_BY = {
    DUE(list) {
        list.sort((a,b)=>a.dueDate - b.dueDate)
        return list
    },
    PRIORITY(list) {
        list.sort((a,b)=>-(a.priority - b.priority))
        return list
    }
}